const {
  version,
  name
} = require('../package.json');
import {
  /* eslint-disable no-unused-vars */
  Style,
  Ybutton,
  Ypagination,
  // Ycommon,
  YbuttonAction,
  YlinkTag,
  Yspin,
  YcustomTree,
  YcustomDatePicker,
  YtableBox,
  YsvgIcon,
  Ybreadcrumb,
  Ytag,
  Ymodal,
  YtableActionButton,
  Ytable,
  YtipsButton,
  YTagInput
} from './module';

// ...
const components = [
  Ybutton,
  Ypagination,
  // Ycommon,
  YbuttonAction,
  YlinkTag,
  Yspin,
  YcustomTree,
  YcustomDatePicker,
  YtableBox,
  YsvgIcon,
  Ybreadcrumb,
  Ytag,
  Ymodal,
  YtableActionButton,
  Ytable,
  YtipsButton,
  YTagInput
  // ...
];

// 
import methods from "./methods";

// vuevueBusVue
import Bus from "./Bus";

const displayVersion = () => {
  Function(`
    window.console && console.log('%c${name} %cV${version} ', 'color: #1890ff;font-weight: 500;font-size: 20px;font-family: Source Sans Pro,Helvetica Neue,Arial,sans-serif;', 'color: #ccc;');
  `)();
};

// installVue
const install = function (Vue) {
  // 
  if (install.installed) return;
  install.installed = true;
  // 
  components.map(component => Vue.component(component.name, component));
  // 
  // components.map(component => Vue.use(component))
  displayVersion();
  Vue.prototype.$YSpin = Yspin; // 
  Vue.prototype.$Bus = Bus; // 
  Vue.prototype.$YGetTableDragHeader = Bus.getTableDragHeader; // 

  // 
  methods.forEach(item => {
    Vue.prototype[item.name] = item.method
  })
};

/*  */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export {
  install, //  // installVue.use()
  Ybutton, // 
  Ypagination,
  YbuttonAction,
  YlinkTag,
  Yspin,
  YcustomTree,
  YcustomDatePicker,
  YtableBox,
  YsvgIcon,
  Ybreadcrumb,
  Ytag,
  Ymodal,
  YtableActionButton,
  Ytable,
  YtipsButton,
  YTagInput
};

export default {
  install, //  // installVue.use()
  Ybutton, // 
  Ypagination,
  YbuttonAction,
  YlinkTag,
  Yspin,
  YcustomTree,
  YcustomDatePicker,
  YtableBox,
  YsvgIcon,
  Ybreadcrumb,
  Ytag,
  Ymodal,
  YtableActionButton,
  Ytable,
  YtipsButton,
  YTagInput
};